import md5 from '../../static/js/md5.js';
import rs from '../../static/js/request.js';
let {
	log
} = console;
const app = getApp().globalData;
const {
	appid,
	appsecret,
	imgRemote,
	navBar
} = app;
export default {
	data() {
		return {
			navBar: navBar,
			id: '',
			is_miniBind: uni.getStorageSync('is_miniBind'),
			list: [],
			isDisable: true,
			count: 0
		};
	},
	methods: {
		leftClick() {
			uni.navigateBack({
				delta: 1
			});
		},
		cancelPay() {
			uni.switchTab({
				url: '/pages/tabar/shopcart'
			})
		},
		bindWechat() {
			this.count++;
			if (this.count != 1) return;
			setTimeout(() => {
				this.count = 0
			}, 1000)
			rs.Toast('该账号未绑定微信，请先绑定再来支付');
			setTimeout(() => {
				uni.switchTab({
					url: '/pages/tabar/user'
				})
			}, 1000)

		},
























































































































































		payOrder() {
			console.log(132)
			let {
				id
			} = this;
			let timeStamp = Math.round(new Date().getTime() / 1000);
			let obj = {
				appid: appid,
				timeStamp: timeStamp,
				oid: id,
				type: 'mini'
			};
			let sign = md5.hexMD5(rs.objKeySort(obj) + appsecret);
			let params = Object.assign({
				sign: sign,
			}, obj)
			rs.postRequests("payTemOrder", params, (res) => {
				let {
					data
				} = res;

				if (data.code == 200) {
					if (data.data.payType == 1) {
						rs.Toast('支付成功');
						setTimeout(() => {
							uni.switchTab({
								url: '/pages/tabar/order',
							})
						}, 1000)

					}
					log(res.data.data.wxParams);
					if (res.data.data.payType == 2) {
						let {
							wxParams
						} = res.data.data;

						uni.requestPayment({
							provider: 'wxpay',
							orderInfo: wxParams, //微信、支付宝订单数据
							timeStamp: wxParams.timeStamp,
							nonceStr: wxParams.nonceStr,
							package: wxParams.package,
							signType: wxParams.signType,
							paySign: wxParams.paySign,
							success: function(res) {
								console.log('success:' + JSON.stringify(res));
								rs.Toast('支付成功');
								setTimeout(() => {
									uni.switchTab({
										url: '/pages/tabar/order',
									})
								}, 1000)
							},
							fail: function(err) {
								console.log(err)
								rs.Toast("充值失败");
							}
						});

					}
				} else {
					rs.Toast(data.msg)
				}
			})
		},


































































		// 临时订单信息
		temOrder() {
			let timeStamp = Math.round(new Date().getTime() / 1000);
			let obj = {
				appid: appid,
				timeStamp: timeStamp,
				id: this.id
			};
			let sign = md5.hexMD5(rs.objKeySort(obj) + appsecret);
			let params = Object.assign({
				sign: sign,
			}, obj)
			rs.getRequests("temOrderMsg", params, (res) => {
				let {
					data
				} = res;
				if (data.code == 200) {
					this.list = data.data
				} else {
					rs.Toast(data.msg);
				}

			})
		}
	},
	onShow() {
		this.temOrder();
	},
	onLoad(option) {
		this.id = option.id;
	}
};
